<template>
	<div class="pa-md-6 pa-sm-3">
		<v-row>
			<v-col cols="12" sm="12">
				<TabelStartup/>
			</v-col>
			<v-col cols="12" md="6" sm="12">
				<DaftarStartup/>
			</v-col>
			<v-col cols="12" md="6" sm="12">
				<DaftarTraction />				
			</v-col>
		</v-row>

		<v-alert 
			class="mt-3"
			v-if="detailTraction === null" 
			type="info">
			Silahkan pilih STARTUP dan TRACTION terlebih dahulu
		</v-alert>

		<v-col 
			v-if="isLoading"
			cols="12" md="6" sm="12">
				<Loader type="card" />
		</v-col>
		<component v-else :is="grafik" :key="changingKey"></component>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import DaftarStartup from '@/components/Startup/Dashboard/DaftarStartup'
	import TabelStartup from '@/components/Startup/Dashboard/TabelStartup'
	import DaftarTraction from '@/components/Startup/Dashboard/DaftarTraction'
	import Grafik from '@/components/Startup/Dashboard/Grafik'
	import Loader from '@/components/Loader'

	export default{
		data: () => ({
			grafik: '',
			changingKey: 0
		}),
		components: {
			Grafik,
			DaftarTraction,
			TabelStartup,
			DaftarStartup,
			Loader
		},
		created() {
			this.$store.dispatch('dataStartupTraction/getDataTraction')
			this.$store.dispatch('dataStartupTraction/updateIsLoading', true)
		},
		computed: {
			...mapState('dataStartupDashboard', ['dataStartup', 'detailTraction']),			
			...mapState('dataStartupTraction', ['dataResponse', 'isLoading'])	
		},

		watch: {
			detailTraction(val) {
				const payload = {
					traction: val.id,
					startup: this.dataStartup.map(x => x.id)
				}
				this.$store.dispatch('dataStartupTraction/postDataTractionShow', payload)
				this.$store.dispatch('dataStartupTraction/updateIsLoading', true)
			},
			dataStartup() {
				// this.changingKey++
				this.grafik = ''
			},
			dataResponse() {
				this.grafik = 'Grafik'
				this.changingKey++
			}
		}
	}
</script>