<template>	
	<v-row>
		<v-col 
			v-for="item in dataGrafik" :key="item.id"
			cols="12" md="6" sm="12">
			<v-card>
				<v-card-title class="py-2">
					{{ item.pertanyaan }}					
				</v-card-title>
				<v-card-text>
					<canvas :id="`grafik-${item.id}`"></canvas>
				</v-card-text>
			</v-card>
		</v-col>
	</v-row>
</template>

<script>
	import { mapState } from 'vuex'
	import Chart from 'chart.js'

	const dataChart = {
		type: 'bar',		
		options: {
			responsive: true,
			scales: {
				yAxes: [
					{
						ticks: {
							beginAtZero: true
						}
					}
				]
			}
		}
	}

	export default{
		data: () => ({
			dataChart: dataChart,
			color: ['#EF9A9A', '#90CAF9', '#A5D6A7', '#FFF59D', '#FFCC80', '#CE93D8']
		}),

		mounted() {
			this.createChart(this.dataGrafik)
		},

		computed: {
			...mapState('dataStartupDashboard', ['dataStartup', 'detailTraction']),			
			...mapState('dataStartupTraction', ['dataResponse', 'isLoading']),	
			dataGrafik() {
				return this.detailTraction.pertanyaan
					.map((tanya, i) => {
						const datasets = this.dataResponse
							.map(res => {
								return res.traction
									.filter(traction => {
										if (traction.pertanyaan === tanya.pertanyaan) {
											traction.startup = res.name
											return traction
										}
									})
							})							
							.flat()

						return {
							id: i+1,
							pertanyaan: tanya.pertanyaan,
							datasets: datasets
						}
					})
				// return [
				// 	{
				// 		id: 1,
				// 		pertanyaan: 'apa ?',
				// 		datasets: [{
				// 			startup: 'Smartedu',
				// 			jawaban: 100
				// 		},{
				// 			startup: 'Matrash',
				// 			jawaban: 90
				// 		}]
				// 	}
				// ]				
			}		
		},

		methods: {
			createChart(chartData) {
				chartData.map(x => {
					const cfx = document.getElementById(`grafik-${x.id}`)
					new Chart(cfx, {
						type: this.dataChart.type,
						data: {
							labels: [''],
							datasets: x.datasets.map((y, i) => {
								return {
									label: y.startup,
									data: [parseInt(y.jawaban)],
									backgroundColor: this.color[i],
								}
							})
						},
						options: this.dataChart.options
					})
				})
			}
		}
	}
</script>