<template>
	<v-card>
		<v-card-title class="py-2">
			Daftar Startup yang terpilih
		</v-card-title>
		<v-card-text style="overflow: auto;max-height: 200px;">
      <v-alert 
        v-if="dataStartup.length === 0"
        type="info">
        Silahkan pilih STARTUP terlebih dahulu
      </v-alert>
			<v-list
        v-else
        dense>
        <template
          v-for="(item, i) in dataStartup">
          <v-list-item
            :key="i">
            <v-list-item-avatar>
              <v-avatar size="40" :color="`${color[i]} lighten-3`"></v-avatar>
            </v-list-item-avatar>

            <v-list-item-content two-line>
              <v-list-item-title>{{item.startup.name}}</v-list-item-title>
              <v-list-item-subtitle>{{item.category}}</v-list-item-subtitle>
              <v-divider></v-divider>
            </v-list-item-content>
          </v-list-item>
        </template>
			</v-list>
		</v-card-text>
	</v-card>
</template>

<script>
  import { mapState } from 'vuex'

  export default{
    data: () => ({
      color: ['red', 'blue', 'green', 'yellow', 'orange', 'purple']
    }),
    computed: {
      ...mapState('dataStartupDashboard', ['dataStartup'])
    }
  }
</script>