<template>
	<v-card>
		<v-card-title class="py-2">
			Daftar Traction
		</v-card-title>
    <v-card-text v-if="dataStartup.length === 0"  style="max-height: 200px;">
      <v-alert         
        type="info">
        Silahkan pilih STARTUP terlebih dahulu
      </v-alert>
    </v-card-text>
		<v-card-text v-else style="overflow: auto;max-height: 200px;">
      <Loader type="list-item-avatar" v-if="isLoading" />
			<v-list v-else dense>
        <v-list-item-group v-model="list">
          <template
            v-for="item in dataTraction">
            <v-list-item
              link
              @click="selected(item)"
              :key="item.id">
              <v-list-item-icon>
                <v-icon color="cyan">
                  mdi-trello
                </v-icon>
              </v-list-item-icon>

              <v-list-item-content>
                <v-list-item-title>
                  {{ item.judul }}
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </template>
        </v-list-item-group>
			</v-list>
		</v-card-text>
	</v-card>
</template>

<script>
  import { mapState } from 'vuex'
  import Loader from '@/components/Loader'
  export default{
    data: () => ({
      list: null
    }),
    computed: {
      ...mapState('dataStartupTraction', ['dataTraction', 'isLoading']),
      ...mapState('dataStartupDashboard', ['dataStartup'])
    },
    components: { Loader },
    methods: {
      selected(val) {
        this.$store.dispatch('dataStartupDashboard/updateDataTraction', val)
      }
    }
  }
</script>